      }
      25% {
        transform: translate(10px, -25px) scale(1.05);
      }
      50% {
        transform: translate(-10px, 10px) scale(0.95);
      }
      75% {
        transform: translate(25px, 25px) scale(1.02);
      }
    }
  }
  @keyframes gradient {
    0%, 100% {
      background-position: 0% 50%;
    }
    50% {
      background-position: 100% 50%;
    }
